.profile {
  padding: 40px 0 51px;
  max-width: 880px;
  /*margin: 0 auto;*/
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}

@media screen and (max-width: 872px) {
  .profile {
    flex-direction: column;
    padding: 42px 0 36px;
  }
}
